<template>
  <div>
    <navBedRoom/>
    <div class="container bg-light">
      <h1 class="p-5 text-dark text-center">
        <b>Listado de Habitaciones</b>
      </h1>
      <b-modal id="modal-bedroom" ref="myModalRef" hide-footer title="Eliminar Habitación">
        <div class="container">
          <div class="row">
            <p>¿Está seguro que desea eliminar la habitación?</p>
            <div class="col-md-6 pl-0">
              <a
                href="#"
                @click="deleteById(selectedID)"
                class="btn btn-secondary btn-sm btn-block"
              >Confirmar</a>
            </div>
            <div class="col-md-6 pr-0">
              <a
                href="#"
                @click="$refs.myModalRef.hide()"
                class="btn btn-tertiary btn-sm btn-block"
              >Cancelar</a>
            </div>
          </div>
        </div>
      </b-modal>
      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Buscar" class="mb-0">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Escriba para buscar"></b-form-input>
              <b-input-group-append>
                <b-button :disable="!filter" @click="filter=''">Limpiar</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Ordenar por" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- Seleccione --</option>
              </b-form-select>
              <b-form-select v-model="sortDesc" :disabled="!sortBy" slot="append">
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>
      <b-table
        id="table-bedroom"
        show-empty
        hover
        stacked="xl"
        fixed
        empty-filtered-text="No se encontrarón registros en su búsqueda"
        empty-text="No hay datos para mostrar"
        :items="bedrooms"
        :fields="columns"
        :filter="filter"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        :current-page="currentPage"
        :per-page="10"
        @filtered="onFiltered"
      >
        <template slot="piso" slot-scope="data">{{data.value.descripcion}}</template>
        <template slot="estadoHabitacion" slot-scope="data">
          <span
            v-if="data.value.descripcion==='Ocupada'"
            class="btn btn-danger"
          >{{data.value.descripcion}}</span>
          <span v-else class="btn btn-success">{{data.value.descripcion}}</span>
        </template>
        <template slot="tipoHabitacion" slot-scope="data">{{data.value.descripcion}}</template>
        <template slot="genero" slot-scope="data">{{data.value.descripcion}}</template>
        <template slot="actions" slot-scope="data">
          <router-link class="btn btn-warning mb-1 mr-1" :to="`/habitacion/${data.item.id}/editar`">
            <i class="fa fa-bed"></i>
          </router-link>
          <button class="btn btn-danger mb-1" type="button" @click="getId(data.item.id)">
            <i class="fa fa-trash-alt"></i>
          </button>
        </template>
      </b-table>
      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-pagination
            size="md"
            :total-rows="totalRows"
            v-model="currentPage"
            :per-page="10"
            @input="find(currentPage)"
          ></b-pagination>
        </b-col>
      </b-row>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import navBedRoom from "@/components/bedroom/navBedRoom.vue";
import axios from "axios";
import Footer from "@/components/Footer.vue";
export default {
  name: "habitaciones",
  data() {
    return {
      columns: [
        {
          key: "piso"
        },
        {
          key: "tipoHabitacion",
          label: "Tipo"
        },
        {
          key: "genero",
          label: "Género",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "numero"
        },
        {
          key: "estadoHabitacion",
          label: "Estado",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "actions",
          label: "Acciones"
        }
      ],
      estadoHabitacion: {
        id: null
      },
      bedrooms: [],
      bedroom: [],
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      isSelected: null,
      selectedID: null,
      totalRows: null,
      currentPage: 1
    };
  },
  methods: {
    async find(currentPage) {
      axios
        .get(`${URL}/api/habitacion?page=${currentPage - 1}`)
        .then(response => {
          this.bedroom = response.data.content;
          this.totalRows = response.data.totalElements;
        })
        .catch(error => console.log(error.message));
    },
    async findAll() {
      axios
        .get(`${URL}/api/habitaciones`)
        .then(response => (this.bedrooms = response.data))
        .catch(error => console.log(error.message));
    },
    deleteById(id) {
      axios
        .delete(`${URL}/api/habitacion/${id}`)
        .then(response => {
          this.$refs.myModalRef.hide();
          this.bedrooms = this.bedrooms.filter(bedroom => bedroom.id != id);
          bootbox.alert({
            title: "Eliminado Correctamente",
            message: response.data.mensaje,
            backdrop: true,
            locale: "es"
          });
        })
        .catch(error => {
          this.$refs.myModalRef.hide();
          if (error.response.status === 404) {
            bootbox.alert({
              title: "Error",
              message: error.response.data.mensaje,
              locale: "es",
              backdrop: true
            });
            console.log("Error: " + error.response.status + " Not Found");
          }
          if (error.response.status === 409) {
            bootbox.alert({
              title: "Error",
              message: error.response.data.mensaje,
              locale: "es",
              backdrop: true
            });
            console.log("Error: " + error.response.status + " Conflict");
          }
        });
    },
    getId(id) {
      this.selectedID = id;
      this.$refs.myModalRef.show();
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  },
  computed: {
    sortOptions() {
      return this.columns
        .filter(f => f.sortable)
        .map(f => {
          return {
            text: f.label,
            value: f.key
          };
        });
    }
  },
  mounted(currentPage) {
    this.find(currentPage);
    this.findAll();
  },
  components: {
    navBedRoom,
    Footer
  }
};
</script>

